import React from "react";
import imagine from '../absent_poster.jpg';

const img_default = imagine;

const Movie = ({movie})=>{
  const poster =
    movie.Poster === "N/A" ? img_default : movie.Poster;

  return (
    <div className="movie">
      <h2>{movie.Title}</h2>
      <div>
        <img
          width="200"
          alt={`The movie titled: ${movie.Title}`}
          src={poster}
        />
      </div>
      <p>({movie.Year})</p>
    </div>
  );
}

export default Movie;
